/* Hover  */
.content[data-v-713f9e71] {
  position: relative;
  width: 100%;
  overflow: hidden;
}
.content .content-overlay[data-v-713f9e71] {
  background: orangered;
  position: absolute;
  height: 100%;
  width: 100%;
  text-align: left;
  padding-left: 15px;
  -webkit-transition: all 0.4s ease-in-out 0s;
  transition: all 0.4s ease-in-out 0s;
}
.content:hover .content-overlay[data-v-713f9e71] {
  opacity: 0;
}
.content-image[data-v-713f9e71] {
  width: 100%;
}
.content-details[data-v-713f9e71] {
  position: absolute;
  text-align: center;
  padding-left: 1em;
  padding-right: 1em;
  width: 100%;
  top: 50%;
  left: 50%;
  opacity: 0;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  -webkit-transition: all 0.3s ease-in-out 0s;
  transition: all 0.3s ease-in-out 0s;
}
.content:hover .content-details[data-v-713f9e71] {
  top: 50%;
  left: 50%;
  opacity: 1;
}
